a {
  color: #1677ff;
  text-decoration: none;
}

ul {
  padding: 0;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  display: flex;
  justify-content: center;
  z-index: 999;
}
/* -------------------------- */
.search {
  padding: 0 70px;
}

.search__sort {
  display: flex;
  justify-content: end;
}

.search__button {
  border-radius: 0 10px 10px 0;
  padding: 7px 11px;
  height: 50px;
}

.search__sort svg {
  padding: 30px 0 0 0;
  font-size: 25px;

}

.search__favorites {
  margin-left: 40px;
  font-size: 25px;
}

.search__favorites:hover {
  color: #1677ff;
}

.search__favorites:focus {
  color: #3f94ac;
}

.search__input {
  border-radius: 10px 0px 0px 10px;
}

.search__title {
  text-align: center;
  font-size: 25px;
  margin-bottom: 20px;
}
/* -------------------------- */

.login {
  padding: 30px;
}

.login__form {
  margin: 0 auto;
}

.login__title {
  text-align: center;
  font-size: 20px;
  margin-bottom: 20px;
}
/* -------------------------- */


.route__menu {
  padding: 0 80px;
  display: flex;
  align-items: center;
}

.route__menu ul {
  display: flex;
  list-style: none;
  gap: 20px;
}

.route__menu div {
  font-size: 20px;
  color: #1677ff;
  margin-left: auto;
}
/* -------------------------- */

.menu__item {
  font-size: 18px;

}

/* -------------------------- */

.favorites {
  padding: 0 40px;
}

.favorites__title {
  text-align: center;
  font-size: 30px;
}

.favorites__item .ant-card-body {
  display: flex;
  font-size: 18px;
}

.favorites__buttons {
  margin-left: auto;
}

/* -------------------------- */
.row {
  display: flex;
  align-items: center;
}

.column {
  display: block;
}

.row .item__content {
  margin-left: 40px;
}

.row .item__header {
  font-size: 18px;
}